
"use client";

import { useState, useMemo, type Key, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription, CardFooter } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { DropdownMenu, DropdownMenuContent, DropdownMenuCheckboxItem, DropdownMenuLabel, DropdownMenuSeparator, DropdownMenuTrigger } from "@/components/ui/dropdown-menu";
import { Input } from "@/components/ui/input";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { FileText, Eye, Search as SearchIcon, Filter as FilterIcon, ArrowUpDown, ChevronLeft, ChevronRight, PencilLine, Trash2, CheckCircle, XCircle } from "lucide-react";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog";
import { cn } from '@/lib/utils';

interface Expense {
  id: string;
  date: string;
  category: string;
  description: string;
  amount: number;
  status: "Pending" | "Approved" | "Rejected";
}

const initialExpenses: Expense[] = [
  { id: "EXP001", date: "2024-07-01", category: "Office Supplies", description: "Printer paper and pens", amount: 45.50, status: "Approved" },
  { id: "EXP002", date: "2024-07-03", category: "Software Subscription", description: "Monthly SaaS tool", amount: 99.00, status: "Approved" },
  { id: "EXP003", date: "2024-07-05", category: "Travel", description: "Client meeting taxi fare", amount: 25.00, status: "Pending" },
  { id: "EXP004", date: "2024-07-08", category: "Marketing", description: "Social media ad campaign", amount: 250.00, status: "Approved" },
  { id: "EXP005", date: "2024-07-10", category: "Utilities", description: "Office internet bill", amount: 75.00, status: "Rejected" },
  { id: "EXP006", date: "2024-07-12", category: "Office Supplies", description: "New keyboards (2 units)", amount: 120.00, status: "Pending" },
  { id: "EXP007", date: "2024-07-15", category: "Meals", description: "Team lunch", amount: 85.75, status: "Approved" },
];

type SortKey = keyof Expense | null;
type SortDirection = "ascending" | "descending";

interface SortConfig {
  key: SortKey;
  direction: SortDirection;
}

interface ColumnConfig {
  id: keyof Expense | 'actions';
  label: string;
  canHide: boolean;
  sortable: boolean;
  minWidth?: string;
  className?: string;
  headerClassName?: string;
}

const columnsConfig: ColumnConfig[] = [
  { id: 'id', label: 'Expense ID', canHide: false, sortable: true, minWidth: "120px" },
  { id: 'date', label: 'Date', canHide: true, sortable: true, minWidth: "120px" },
  { id: 'category', label: 'Category', canHide: true, sortable: true, minWidth: "150px" },
  { id: 'description', label: 'Description', canHide: true, sortable: true, minWidth: "250px" },
  { id: 'amount', label: 'Amount', canHide: true, sortable: true, className: "text-right", minWidth: "100px" },
  { id: 'status', label: 'Status', canHide: true, sortable: true, minWidth: "120px" },
  { id: 'actions', label: 'Actions', canHide: false, sortable: false, className: "text-right", minWidth: "180px" },
];

const EXPENSE_STATUSES: Expense["status"][] = ["Pending", "Approved", "Rejected"];

export default function ExpensesPage() {
  const [expenses, setExpenses] = useState<Expense[]>(initialExpenses);
  const [sortConfig, setSortConfig] = useState<SortConfig>({ key: null, direction: 'ascending' });

  const initialVisibility = columnsConfig.reduce((acc, col) => {
    acc[col.id as string] = true;
    return acc;
  }, {} as Record<string, boolean>);
  const [columnVisibility, setColumnVisibility] = useState<Record<string, boolean>>(initialVisibility);

  const [searchTerm, setSearchTerm] = useState("");
  const [statusFilter, setStatusFilter] = useState<"All" | Expense["status"]>("All");
  const [categoryFilter, setCategoryFilter] = useState<string>("All"); // Example: can add category filter
  const [rowsPerPage, setRowsPerPage] = useState(5);
  const [currentPage, setCurrentPage] = useState(1);
  const [pageInput, setPageInput] = useState(currentPage.toString());

  const [showRejectConfirm, setShowRejectConfirm] = useState(false);
  const [expenseToUpdate, setExpenseToUpdate] = useState<Expense | null>(null);
  const [actionType, setActionType] = useState<"Approve" | "Reject" | null>(null);


  const uniqueCategories = useMemo(() => {
    const categories = new Set(initialExpenses.map(exp => exp.category));
    return ["All", ...Array.from(categories)];
  }, []);

  useEffect(() => {
    setPageInput(currentPage.toString());
  }, [currentPage]);

  const handlePageInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setPageInput(e.target.value);
  };

  const handlePageInputSubmit = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter') {
      const newPage = parseInt(pageInput, 10);
      if (!isNaN(newPage) && newPage >= 1 && newPage <= totalPages) {
        setCurrentPage(newPage);
      } else {
        setPageInput(currentPage.toString());
      }
    }
  };

  const filteredExpenses = useMemo(() => {
    return expenses.filter(expense => {
      const searchLower = searchTerm.toLowerCase();
      const matchesSearchTerm =
        expense.id.toLowerCase().includes(searchLower) ||
        expense.category.toLowerCase().includes(searchLower) ||
        expense.description.toLowerCase().includes(searchLower);
      const matchesStatus = statusFilter === 'All' || expense.status === statusFilter;
      const matchesCategory = categoryFilter === 'All' || expense.category === categoryFilter;
      return matchesSearchTerm && matchesStatus && matchesCategory;
    });
  }, [expenses, searchTerm, statusFilter, categoryFilter]);

  const sortedExpenses = useMemo(() => {
    let sortableItems = [...filteredExpenses];
    if (sortConfig.key !== null) {
      sortableItems.sort((a, b) => {
        const aVal = a[sortConfig.key!];
        const bVal = b[sortConfig.key!];
        if (typeof aVal === 'number' && typeof bVal === 'number') {
          return sortConfig.direction === 'ascending' ? aVal - bVal : bVal - aVal;
        }
        if (typeof aVal === 'string' && typeof bVal === 'string') {
          return sortConfig.direction === 'ascending' ? aVal.localeCompare(bVal) : bVal.localeCompare(aVal);
        }
        return 0;
      });
    }
    return sortableItems;
  }, [filteredExpenses, sortConfig]);

  const totalPages = useMemo(() => Math.max(1, Math.ceil(sortedExpenses.length / rowsPerPage)), [sortedExpenses.length, rowsPerPage]);
  const paginatedExpenses = useMemo(() => {
    const startIndex = (currentPage - 1) * rowsPerPage;
    return sortedExpenses.slice(startIndex, startIndex + rowsPerPage);
  }, [sortedExpenses, currentPage, rowsPerPage]);

  const requestSort = (key: SortKey) => {
    if (!key) return;
    let direction: SortDirection = 'ascending';
    if (sortConfig.key === key && sortConfig.direction === 'ascending') {
      direction = 'descending';
    }
    setSortConfig({ key, direction });
  };

  const getSortIcon = (key: SortKey) => {
    if (sortConfig.key !== key) return <ArrowUpDown className="ml-2 h-4 w-4 opacity-50" />;
    return <ArrowUpDown className="ml-2 h-4 w-4 text-primary" />;
  };

  const toggleColumnVisibility = (columnId: string) => {
    setColumnVisibility(prev => ({ ...prev, [columnId]: !prev[columnId] }));
  };
  
  const handleApproveExpense = (expense: Expense) => {
    setExpenses(prev => prev.map(exp => exp.id === expense.id ? { ...exp, status: "Approved" } : exp));
  };

  const handleRejectExpenseClick = (expense: Expense) => {
    setExpenseToUpdate(expense);
    setActionType("Reject");
    setShowRejectConfirm(true);
  };

  const handleConfirmUpdateStatus = () => {
    if (expenseToUpdate && actionType) {
      setExpenses(prevExpenses =>
        prevExpenses.map(e =>
          e.id === expenseToUpdate.id ? { ...e, status: actionType === "Approve" ? "Approved" : "Rejected" } : e
        )
      );
    }
    setShowRejectConfirm(false);
    setExpenseToUpdate(null);
    setActionType(null);
  };

  const handleViewExpense = (expense: Expense) => console.log("Viewing expense:", expense.id);
  const handleEditExpense = (expense: Expense) => console.log("Editing expense:", expense.id);

  const getStatusBadgeVariant = (status: Expense["status"]): "default" | "secondary" | "destructive" | "outline" => {
    switch (status) {
      case "Pending": return "default";
      case "Approved": return "secondary"; // Using 'secondary' for green-like in default theme
      case "Rejected": return "destructive";
      default: return "outline";
    }
  };

  return (
    <div className="space-y-8">
      <Card className="shadow-md w-full mx-auto">
        <CardHeader className="p-2.5">
          <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-x-4 gap-y-2">
            <CardTitle className="whitespace-nowrap flex-shrink-0 flex items-center">
              <FileText className="mr-2 h-5 w-5 text-primary" /> Manage Expenses
            </CardTitle>
            <div className="flex flex-1 min-w-0 items-center gap-2">
              <div className="relative flex-grow">
                <SearchIcon className="absolute left-2.5 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
                <Input
                  placeholder="Search expenses..."
                  value={searchTerm}
                  onChange={(e) => { setSearchTerm(e.target.value); setCurrentPage(1); }}
                  className="pl-8 w-full"
                />
              </div>
              <DropdownMenu>
                <DropdownMenuTrigger asChild>
                  <Button variant="outline" size="icon" className="flex-shrink-0"><Eye className="h-4 w-4" /><span className="sr-only">Toggle Columns</span></Button>
                </DropdownMenuTrigger>
                <DropdownMenuContent align="end">
                  <DropdownMenuLabel>Toggle Columns</DropdownMenuLabel><DropdownMenuSeparator />
                  {columnsConfig.filter(col => col.canHide).map(column => (
                    <DropdownMenuCheckboxItem key={column.id} checked={columnVisibility[column.id as string]} onCheckedChange={() => toggleColumnVisibility(column.id as string)}>
                      {column.label}
                    </DropdownMenuCheckboxItem>
                  ))}
                </DropdownMenuContent>
              </DropdownMenu>
              <Select value={statusFilter} onValueChange={(value) => { setStatusFilter(value as "All" | Expense["status"]); setCurrentPage(1); }}>
                <SelectTrigger className="w-auto min-w-[40px] flex-shrink-0 p-2.5"><FilterIcon className="h-4 w-4 text-muted-foreground" /><span className="sr-only"><SelectValue placeholder="Status" /></span></SelectTrigger>
                <SelectContent>
                  <SelectItem value="All">All Statuses</SelectItem>
                  {EXPENSE_STATUSES.map(status => <SelectItem key={status} value={status}>{status}</SelectItem>)}
                </SelectContent>
              </Select>
               <Select value={categoryFilter} onValueChange={(value) => { setCategoryFilter(value); setCurrentPage(1); }}>
                <SelectTrigger className="w-auto min-w-[40px] flex-shrink-0 p-2.5"><FilterIcon className="h-4 w-4 text-muted-foreground" /><span className="sr-only"><SelectValue placeholder="Category" /></span></SelectTrigger>
                <SelectContent>
                  {uniqueCategories.map(category => <SelectItem key={category} value={category}>{category}</SelectItem>)}
                </SelectContent>
              </Select>
            </div>
          </div>
          <CardDescription className="mt-2">Track and manage company expenses. ({filteredExpenses.length} expenses found)</CardDescription>
        </CardHeader>
        <CardContent className="p-0 overflow-x-auto">
          <Table>
            <TableHeader>
              <TableRow>
                {columnsConfig.map(column => columnVisibility[column.id as string] && (
                  <TableHead key={column.id} style={column.minWidth ? { minWidth: column.minWidth } : {}} className={cn("text-center", column.headerClassName)} onClick={() => column.sortable && requestSort(column.id as keyof Expense)}>
                    <div className={cn("flex items-center justify-center", column.sortable ? 'cursor-pointer hover:text-foreground' : '')}>
                      <span className="whitespace-nowrap">{column.label}</span>{column.sortable && getSortIcon(column.id as keyof Expense)}
                    </div>
                  </TableHead>
                ))}
              </TableRow>
            </TableHeader>
            <TableBody>
              {paginatedExpenses.map(expense => (
                <TableRow key={expense.id}>
                  {columnVisibility['id'] && <TableCell className="font-medium text-center">{expense.id}</TableCell>}
                  {columnVisibility['date'] && <TableCell className="text-muted-foreground text-center">{expense.date}</TableCell>}
                  {columnVisibility['category'] && <TableCell className="text-center">{expense.category}</TableCell>}
                  {columnVisibility['description'] && <TableCell>{expense.description}</TableCell>}
                  {columnVisibility['amount'] && <TableCell className="text-right">${expense.amount.toFixed(2)}</TableCell>}
                  {columnVisibility['status'] && <TableCell className="text-center"><Badge variant={getStatusBadgeVariant(expense.status)}>{expense.status}</Badge></TableCell>}
                  {columnVisibility['actions'] && (
                    <TableCell className="text-right">
                      <div className="flex items-center justify-end gap-1">
                        <Button size="icon" onClick={() => handleViewExpense(expense)} aria-label="View expense" className="bg-blue-500 hover:bg-blue-600 text-white"><Eye className="h-4 w-4" /></Button>
                        <Button size="icon" onClick={() => handleEditExpense(expense)} aria-label="Edit expense" className="bg-yellow-500 hover:bg-yellow-600 text-black"><PencilLine className="h-4 w-4" /></Button>
                        {expense.status === "Pending" && (
                          <>
                            <Button size="icon" onClick={() => handleApproveExpense(expense)} aria-label="Approve expense" className="bg-green-500 hover:bg-green-600 text-white"><CheckCircle className="h-4 w-4" /></Button>
                            <Button size="icon" onClick={() => handleRejectExpenseClick(expense)} aria-label="Reject expense" className="bg-red-500 hover:bg-red-600 text-white"><XCircle className="h-4 w-4" /></Button>
                          </>
                        )}
                      </div>
                    </TableCell>
                  )}
                </TableRow>
              ))}
            </TableBody>
          </Table>
          {paginatedExpenses.length === 0 && <div className="border border-dashed border-border rounded-md p-8 text-center mt-4"><p className="text-muted-foreground">No expenses found.</p></div>}
        </CardContent>
        <CardFooter className="flex flex-row items-center justify-between gap-4 border-t p-2.5">
          <div className="flex flex-col items-start text-sm text-muted-foreground space-y-1">
            <span className="text-xs">Rows</span>
            <Select value={rowsPerPage.toString()} onValueChange={value => { setRowsPerPage(Number(value)); setCurrentPage(1); }}>
              <SelectTrigger className="w-[70px]"><SelectValue placeholder={rowsPerPage.toString()} /></SelectTrigger>
              <SelectContent>{[5, 10, 15, 20].map(size => <SelectItem key={size} value={size.toString()}>{size}</SelectItem>)}</SelectContent>
            </Select>
          </div>
          <div className="flex flex-col items-end space-y-1">
            <span className="text-xs text-muted-foreground">Page {totalPages === 0 ? 0 : currentPage} of {totalPages}</span>
            <div className="flex items-center space-x-1">
              <Button variant="outline" size="icon" onClick={() => setCurrentPage(prev => Math.max(prev - 1, 1))} disabled={currentPage === 1 || totalPages === 0} aria-label="Previous page"><ChevronLeft className="h-4 w-4" /></Button>
              <Input type="number" value={pageInput} onChange={handlePageInputChange} onKeyDown={handlePageInputSubmit} className="h-9 w-14 text-center px-1" min="1" max={totalPages} disabled={totalPages === 0} aria-label="Page number" />
              <Button variant="outline" size="icon" onClick={() => setCurrentPage(prev => Math.min(prev + 1, totalPages))} disabled={currentPage === totalPages || totalPages === 0} aria-label="Next page"><ChevronRight className="h-4 w-4" /></Button>
            </div>
          </div>
        </CardFooter>
      </Card>

      <AlertDialog open={showRejectConfirm} onOpenChange={setShowRejectConfirm}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>Confirm Action</AlertDialogTitle>
            <AlertDialogDescription>
              Are you sure you want to {actionType?.toLowerCase()} expense "{expenseToUpdate?.id}"?
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel onClick={() => { setExpenseToUpdate(null); setActionType(null); }}>Cancel</AlertDialogCancel>
            <AlertDialogAction onClick={handleConfirmUpdateStatus} className={cn(actionType === "Reject" ? "bg-destructive hover:bg-destructive/90" : "bg-primary hover:bg-primary/80")}>
              {actionType}
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    </div>
  );
}
